@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,300;0,600;1,200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body::-webkit-scrollbar {
  display: none;
}

html {
  scroll-behavior: smooth;
}

.body {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  justify-content: space-evenly;
  height: 90vh;
  padding-left: 10px;
}

.subtitle {
  flex: 0.2 1;
  font-size: 1.2em;
  color: white;
  font-family: "Josefin Sans", sans-serif;
  font-style: normal;
  text-align: center;
  align-self: center;
}
.perfil_nome {
  flex: 0.2 1;
  font-size: 4em;
  color: white;
  font-family: "Josefin Sans", sans-serif;
  font-style: normal;
  text-align: center;
  letter-spacing: 0.125em;
  align-self: center;
}

.perfil_foto {
  flex: 0.2 1;
  align-self: center;
  border-radius: 200px;
  border: #fdfdfd solid;
  max-width: 20%;
  object-fit: contain;
  box-shadow: 5px 5px 50px black;
}

.perfil_texto {
  flex: 0.4 1;
  font-size: 20px;
  color: white;
  font-family: "Josefin Sans", sans-serif;
  font-style: normal;
  text-align: center;
  align-self: center;
  text-align: left;
  max-width: 500px;
}

@media all and (max-width: 800px) {
  .body {
    margin-top: 18%;
  }

  .perfil_foto {
    max-width: 40%;
    border-radius: 200px;
  }

  .subtitle {
    font-size: 2em;
  }

  .perfil_nome {
    font-size: 3em;
  }

  .perfil_texto {
    font-size: 1.5em;
  }
}

@media all and (max-width: 500px) {
  .body {
    margin-top: 18%;
  }

  .perfil_foto {
    max-width: 50%;
    border-radius: 100px;
  }

  .subtitle {
    font-size: 1em;
  }

  .perfil_nome {
    font-size: 2em;
  }

  .perfil_texto {
    font-size: 1.5em;
  }
}

.navbar {
  top: 0;
  display: flex;
  justify-content: space-around;
  padding-left: 10px;
  background: rgb(11, 4, 19);
  width: 100%;
  height: 60px;
  color: white;
}

.navbar::-webkit-scrollbar {
  display: none;
}

.middle {
  flex: 1 1;
  display: flex;
  object-fit: contain;
  align-items: center;
  justify-content: space-evenly;
  max-width: 300px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.middle > ul {
  display: flex;
  align-self: center;
}

.navitems {
  list-style: none;
  border-bottom: 4px solid rgb(11, 4, 19);
  font-family: "Titillium Web", sans-serif;
  font-size: x-large;
  cursor: pointer;
  text-align: center;
  height: 100%;
  color: white;
  margin-right: 50px;
  width: 100px;
  transition: border 500ms ease-out;
}

.navitems:hover {
  border-bottom: 4px solid rgb(4, 128, 174);
}

.navitems::-webkit-scrollbar {
  display: none;
}

.about {
  display: flex;
  background: linear-gradient(to bottom, #323232 0%, #3f3f3f 40%, #1c1c1c 150%),
    linear-gradient(
      to top,
      rgba(255, 255, 255, 0.4) 0%,
      rgba(0, 0, 0, 0.25) 200%
    );
  background-blend-mode: multiply;
  height: 100vh;
  flex-direction: column;
  align-items: center;
}

.about::-webkit-scrollbar {
  display: none;
}

.main {
  display: flex;
  background: linear-gradient(to bottom, #323232 0%, #3f3f3f 40%, #1c1c1c 150%),
    linear-gradient(
      to top,
      rgba(255, 255, 255, 0.4) 0%,
      rgba(0, 0, 0, 0.25) 200%
    );
  background-blend-mode: multiply;
  height: 100vh;
  flex-direction: column;
  align-items: center;
}

.main::-webkit-scrollbar {
  display: none;
}

