.navbar {
  top: 0;
  display: flex;
  justify-content: space-around;
  padding-left: 10px;
  background: rgb(11, 4, 19);
  width: 100%;
  height: 60px;
  color: white;
}

.navbar::-webkit-scrollbar {
  display: none;
}

.middle {
  flex: 1;
  display: flex;
  object-fit: contain;
  align-items: center;
  justify-content: space-evenly;
  max-width: 300px;
  width: max-content;
}

.middle > ul {
  display: flex;
  align-self: center;
}
